@font-face {
  font-family: "Bangers";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("./fonts/bangers-v13-latin-regular.woff2") format("woff2"), url("./fonts/bangers-v13-latin-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("./fonts/roboto-v29-latin-regular.woff2") format("woff2"), url("./fonts/roboto-v29-latin-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("./fonts/roboto-v29-latin-700.woff2") format("woff2"), url("./fonts/roboto-v29-latin-700.woff") format("woff");
}
html {
  font-size: clamp(14px, 11px + 0.35vw, 50px);
}

html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  background: #402DB3;
  font-family: Roboto;
  color: white;
  display: flex;
  flex-direction: column;
}

.pages {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}

.page {
  display: none;
}
.page.active {
  display: flex;
}

h1 {
  font-family: Bangers;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.04em;
  margin-bottom: 2em;
  text-align: center;
}

h2 {
  font-family: Bangers;
  font-size: 1.8rem;
  letter-spacing: 0.05em;
  font-weight: 400;
}

.page.home {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.page.country-nerd {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page.country-nerd .letters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.6rem;
  max-width: 50rem;
}
.page.country-nerd .letters .letter {
  height: 4rem;
  width: 4rem;
  border-radius: 0.4rem;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: black;
  font-size: 2rem;
  font-family: Bangers;
  box-shadow: 2px 2px 5px 4px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  will-change: transform;
}
.page.country-nerd .letters .letter:hover {
  transform: translate(0.05rem, 0.05rem);
  box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.15);
}
.page.country-nerd .letters .letter.completed {
  background: radial-gradient(43.04% 43.04% at 67.68% 35.44%, #F3CC3F 0%, #FFE600 0.01%, #FFCB46 85.94%);
}
.page.country-nerd .title-container {
  position: relative;
}
.page.country-nerd .title-container .game-title {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  gap: 1.5rem;
  opacity: 0;
  pointer-events: none;
  font-family: Bangers;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.04em;
}
.page.country-nerd .title-container .letter-placeholder {
  height: 4rem;
  width: 4rem;
  transform: translateY(-1rem);
}

.btn {
  position: relative;
  padding: 1.5rem 3rem;
  width: fit-content;
  min-width: 6rem;
  color: white;
  border-radius: 1000px;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  box-shadow: 0.5rem 0.5rem 3rem 1rem rgba(0, 0, 0, 0.19);
  overflow: hidden;
  transition: transform 0.2s ease;
  will-change: transform;
}
.btn::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #F05050;
  border-radius: 1000px;
  z-index: -1;
}
.btn::after {
  content: "";
  position: absolute;
  height: 30rem;
  width: 30rem;
  top: 50%;
  left: 1%;
  transform: translate(-50%, -50%) scale(0);
  background-color: #ee3939;
  border-radius: 50%;
  z-index: -1;
  transition: transform 0.2s ease;
  will-change: transform;
}
.btn:hover {
  transform: scale(1.05);
}
.btn:hover::after {
  transform: translate(-50%, -50%) scale(1);
}
.btn:active {
  transform: scale(0.95);
}

footer {
  padding: 2rem;
  display: flex;
  justify-content: center;
}
footer .copyrights {
  font-size: 0.7rem;
  opacity: 0.8;
}

.game-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  padding: 2rem;
  width: 15rem;
  max-width: 100%;
}
.game-card::before {
  content: "";
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #231961;
  border-radius: 2rem;
  transition: transform 0.2s ease;
  will-change: transform;
}
.game-card .card-title {
  margin-top: 0;
}
.game-card .card-link {
  color: white;
  text-decoration: none;
  max-width: 10rem;
}
.game-card .card-link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.game-card .card-bottom {
  display: flex;
  justify-content: center;
}
.game-card .card-bottom .btn {
  transition: transform 0.2s ease;
  will-change: transform;
}
.game-card:hover::before {
  transform: scale(1.05);
}
.game-card:hover .btn {
  transform: scale(1.15);
}

header {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
}
header .logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  height: 4rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: white;
  text-decoration: none;
  background-color: #231961;
  border-radius: 1000px;
  padding: 0 1.4rem;
}
header .logo img {
  width: 1.7rem;
}
header .menu {
  height: 4rem;
  width: 4rem;
  background-color: #231961;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  box-shadow: 0.1rem 0.1rem 1rem 0.5rem rgba(0, 0, 0, 0.08);
}
header .menu::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 50%;
  top: 38%;
  right: 26%;
  background-color: white;
}
header .menu::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 40%;
  top: 55%;
  right: 26%;
  background-color: white;
}